<template>
  <div class="suggest">
    <div class="title"></div>
    <div class="content">
        <button type="button" @click='checkoutTag($event)' :data-index="item.index" :class="{'mui-btn': true, 'active': i==index}" v-for='(item, i) in tags' :key="i">{{ item.name }}</button>
    </div>
    <div class="title"></div>
    <textarea name="" id="" cols="30" v-model='text' rows="4" placeholder=","></textarea>
    <div class="addWrap">
        <div class="addPic" @click='sheetVisible=!sheetVisible'></div>
    </div>
    <button @click="submit" type="button" data-loading-text='' data-loading-icon="mui-spinner mui-spinner-custom" class="mui-btn mui-btn-primary"></button>
    <mt-actionsheet
    :actions="actions"
    v-model="sheetVisible">
    </mt-actionsheet>
  </div>
</template>

<script>
import mui from "../lib/mui/js/mui.min.js"
import { Actionsheet, Toast } from 'mint-ui'
export default {
    data(){
        var self = this
        return{
            actions: [
                {name: '', method:function(){
                    self.getWXtoken()
                    // wx.config({
                    //     debug: true, // ,apialertpclogpc
                    //     appId: 'gh_cb13915aaf46', // 
                    //     timestamp: 1, // 
                    //     nonceStr: '', // 
                    //     signature: '',// 1
                    //     jsApiList: [] // JSJS2
                    // });
                    self.openCamera()
                }},
                {name: '',method:function(){
                    self.openPhoto()
                }}
            ],
            sheetVisible: false,
            tags: [
                {name:'', index:0},
                {name:'', index:1},
                {name:'', index:2},
            ],
            index: 0,
            text: ''
        }
    },
    mounted (){

    },
    methods: {
        getWXtoken(){
            var data = {
                access_toke: 1,
                type: jsapi
            }
            this.axios({
                method: 'get',
                url: 'https://api.weixin.qq.com/cgi-bin/ticket/getticket',
                headers: {
                'Content-type': 'application/json'
                }
            }).then(res=>{
                console.log(res);
            })
        },
        submit(e) {   //
            if(this.text.trim()==''){
               Toast('')
               return
            }
            this.text=''
            mui(e.target).button('loading')
            setTimeout(function(){
                Toast('')
                mui(e.target).button('reset')
            },1000)
        },
        openCamera(){   //     :https://mp.weixin.qq.com/debug/wxadoc/dev/api/media-picture.html#wxchooseimageobject
            wx.chooseImage({
                count: 1,  //
                sizeType: ['original','compressed'],    //  
                sourceType: ['album'],  //  
                success: function(res) {
                    // tempFilePathimgsrc
                    var tempFilePaths = res.tempFilePaths
                }
            })
        },
        openPhoto(){     // 
             wx.chooseImage({
                count: 1,  //
                sizeType: ['original','compressed'],    //  
                sourceType: ['camera'],  //  
                success: function(res) {
                    // tempFilePathimgsrc
                    var tempFilePaths = res.tempFilePaths
                }
            })
        },
        checkoutTag(e){
            this.index=e.target.getAttribute('data-index')
        }
    }
}
</script>

<style lang='scss'>
    body {
        background-color: #f8f8f8;
    }
    .suggest {
        .title {
            background-color: #f8f8f8;
            height: 40px;
            line-height: 40px;
            padding-left: 15px;
        }
        .content {
            height: 70px;
            padding-left: 15px;
            button {
                height: 40px;
                width: 80px;
                margin-top: 15px;
                margin-right: 12px;
            }
            .active {
                color: #5285f4;
                border-color: #3e87f6;
                background-color: #ecf3fe;
            }
        }
        textarea {
            border: none;
            font-size: 14px;
            margin-bottom: 10px;
            // padding-bottom: 0px; 
        }
        .addWrap {
            padding: 0 0 15px 15px;
            .addPic {
                width: 50px;
                height: 50px;
                border: 1px dashed #ccc;
                border-radius: 5px;
                background: url(../assets/img/record/add.png) no-repeat center center;
                background-size: 50% 50%;
            }
        }
        .mui-btn-primary {
            position: fixed;
            bottom: 0;
            width: 100%;
            height: 40px;
            background-color: #9fc7fa;
            border: none;
            font-size: 16px;
        }
    }
</style>



